<template>
  <div class="inputBox">
    <input
      type="text"
      class="addList"
      v-model="newTodoItem"
      placeholder="Ìï† ÏùºÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî."
    />
    <button type="button" class="addBtn" @click="addTodo">‚ûï</button>

    <ErrorModal v-if="showModal" @close="showModal = false">
      <template v-slot:header>üí• Ï£ºÏùò üí•</template>
      <template v-slot:footer>
        <p>Ìï† ÏùºÏùÑ Î∞òÎìúÏãú ÏûÖÎ†•ÌïòÏÖîÏïº Ìï©ÎãàÎã§.</p>
        <button class="closeBtn" @click="showModal = false">Îã´Í∏∞</button>
      </template>
    </ErrorModal>
  </div>
</template>

<script>
import ErrorModal from "./ErrorModal.vue";

export default {
  data() {
    return {
      newTodoItem: "",
      showModal: false,
    };
  },
  methods: {
    addTodo() {
      if (this.newTodoItem !== "") {
        let value = this.newTodoItem && this.newTodoItem.trim();
        this.$emit("addTodo", value); //app.vueÏóê Ï†ÑÎã¨
        this.clearInput();
      } else {
        this.showModal = true;
      }
    },
    clearInput() {
      this.newTodoItem = "";
    },
  },
  components: {
    ErrorModal,
  },
};
</script>

